.barchart-container{
    width: 80%;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}


@media (max-width: 480px) {
    .barchart-container{
        width: 90%;
        margin-top: 12rem;
    }
}